@import 'src/_variables.scss';

.product-list-box {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 3rem;
  background-color: $secondary-color-alt;

  & .product-list-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 0.5rem;

    & .product-list-item {
      height: 400px;
      width: 350px;
      padding: 20px;
      border-radius: 5px;
      margin-top: 2.5rem;
      margin-left: 2.5rem;
      position: relative;
      box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
      background-color: white;

      &_img {
        height: 100px;
        width: 100px;
        background-size: cover;
        background-position: center;
        position: absolute;
        top: -25px;
        left: -25px;
        border-radius: 50%;
        box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
      }

      &_title {
        text-align: center;
        font-size: 1.2rem;
        margin-left: 3rem;
        color: $secondary-color;
      }

      &_delete {
        position: absolute;
        top: 0;
        right: 0;
        margin: 0.5rem;
        color: red;
        cursor: pointer;
        border-radius: 50%;
      }

      &_description {
        margin-top: 1.5rem;
        text-align: justify;
        min-height: 57px;
      }

      &_infos {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 0.5rem;

        & span {
          text-align: center;
          border: solid 2px $main-color-alt;
          margin: 0.2rem;
          width: 90px;
          padding: 5px 0;
          border-radius: 5px;
        }
      }

      &_modifications {
        position: absolute;
        width: 100%;
        bottom: 0;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
        background-color: $main-color-alt;
        text-align: justify;
        height: 30px;
        margin-left: -20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: white;

        & > button {
          width: 50%;
          height: 30px;
          margin: 0;
          padding: 0;
          font-size: 1rem;
          border-bottom-left-radius: 5px;
          border-bottom-right-radius: 5px;
        }
      }

      &_diverse {
        display: flex;
        flex-direction: column;
        text-align: left;
        margin-top: 1rem;
      }

      @media (max-width: 900px) {
      }
    }
  }
}
